import {
	login
} from '../../api/api.js'
import md5 from 'js-md5'
export default {
	data() {
		return {
			activeClass: 0,
			roleList: ["学生", "教职工", "家长"],
			from: {
				"user_name": "5120418210075", //用户名
				"pwd": "123456", //密码
				"type": ""
			}
		};
	},
	methods: {
		//选择登陆角色
		selectRole(index) {
			this.activeClass = index;
			this.from.user_name = "";
			this.from.pwd = "";
		},
		//登录
		submit() {
			console.log(login)
			this.from.type = this.activeClass + 1
			this.from.pwd = md5(this.from.pwd).toUpperCase()
			uni.showLoading({
				title: '登录中..'
			});
			uni.request({
				url: login,
				method: 'POST',
				data: this.from,
				success: (res) => {
					uni.hideLoading(); //结束请求动画
					if (res.data.code == 200) {
						uni.setStorageSync("token",res.data.data.token);
						uni.setStorageSync("userInfo", JSON.stringify(res.data.data));
						uni.switchTab({
							url: '../home/home',
						})
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none',
							duration: 2000
						});
					}
				},
				fail: (error) => {
					console.log(error)
				}
			})
		}
	}
}
